import { useDispatch, useSelector } from "react-redux";
import { API_MOVIES_OPTIONS } from "../utils/constants";
import { addPopularMovies} from "../utils/moviesSlice";
import { useEffect } from "react";

const usePopularMovies=()=>{
  const dispatch = useDispatch();
  const moviesPopular = useSelector((store)=>store.movies.PopularMovies)

  // fetch data from tmdb and update store
  const getmoviesPopular = async()=>{
    const data = await fetch("https://api.themoviedb.org/3/movie/upcoming?language=en-US&page=1",API_MOVIES_OPTIONS)
    const json = await data.json();
    console.log(json);
    dispatch(addPopularMovies(json.results))
  }

  useEffect(()=>{
    !moviesPopular && getmoviesPopular();
  },[])
};

export default  usePopularMovies;